import React from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Bar } from "react-chartjs-2";
import { Chart, registerables } from "chart.js";
Chart.register(...registerables);

function Result() {
  const location = useLocation();
  const { testText, username, password, textareaValue } = location.state;

  //   testText.map((item, index) => {
  //     console.log("index:", index);
  //     console.log("value:", item.value);
  //   });

  //    
  const groups = [
    [5, 13, 16],
    [9, 12, 26],
    [15, 17, 19],
    [14, 20, 25],
    [1, 2, 8],
    [0, 3, 18],
    [4, 10, 24],
    [11, 21, 23],
    [6, 7, 22],
  ];
  const newSum = []; //        .

  //     value   
  const sumValuesInGroup = (groupIndexes) => {
    return groupIndexes.reduce((sum, index) => {
      //   value  .    0 .
      const value = testText[index]?.value || 0;
      return sum + parseInt(value); //   value  .
    }, 0); //  0 .
  };

  //     
  const groupSums = groups.map((groupIndexes) => {
    return sumValuesInGroup(groupIndexes);
  });

  const data = {
    labels: [
      "1",
      "2",
      "3",
      "4",
      "5",
      "6",
      "7",
      "8",
      "9",
    ],
    datasets: [
      {
        label: " ",
        data: groupSums,
        backgroundColor: [
          `rgba(255, 99, 132, 0.7)`, // Red
          `rgba(54, 162, 235, 0.7)`, // Blue
          `rgba(255, 206, 86, 0.7)`, // Yellow
          `rgba(75, 192, 192, 0.7)`, // Green
          `rgba(153, 102, 255, 0.7)`, // Purple
          `rgba(255, 159, 64, 0.7)`, // Orange
          `rgba(255, 0, 0, 0.7)`, // Red
          `rgba(0, 255, 0, 0.7)`, // Green
          `rgba(0, 0, 255, 0.7)`, // Blue
        ],
        borderColor: [
          `rgba(255, 99, 132, 0.2)`, // Red
          `rgba(54, 162, 235, 0.2)`, // Blue
          `rgba(255, 206, 86, 0.2)`, // Yellow
          `rgba(75, 192, 192, 0.2)`, // Green
          `rgba(153, 102, 255, 0.2)`, // Purple
          `rgba(255, 159, 64, 0.2)`, // Orange
          `rgba(255, 0, 0, 0.2)`, // Red
          `rgba(0, 255, 0, 0.2)`, // Green
          `rgba(0, 0, 255, 0.2)`, // Blue
        ],
        borderWidth: 1,
      },
    ],
  };

  const options = {
    scales: {
      x: {
        type: "category", // x  category 
      },
      y: {
        beginAtZero: true,
      },
    },
  };

  groupSums.forEach((sum, index) => {
    newSum.push({ sum, index }); //          .
  });

  return (
    <div>
      <h2
        style={{
          color: "#ffffff",
        }}
      >
         
      </h2>
      <div>
        <p>
          : {username}
          <br />
          : {password}
          <br />
          : {textareaValue}
        </p>
      </div>
      <Bar data={data} width={100} height={50} options={options} />
    </div>
  );
}

export default Result;
